.advantages {
  background-color: #fff5f6;
}
.advantages__container {
  padding-bottom: 97px;
  @media screen and(min-width:768px) {
    padding-bottom: 140px;
  }
}

.advantages__list {
  @media screen and (min-width: 768px) {
    display: flex;
    gap: 60px;
  }
  @media screen and (min-width: 768px) and (max-width: 1279px) {
    padding-right: 40px;
  }
  @media screen and (min-width: 1280px) {
    gap: 30px;
  }
}

.advantages__item {
  @media screen and(max-width:767px) {
    width: 230px;
  }
  @media screen and(min-width:768px) {
    flex-basis: calc((100% - 120px) / 3);
  }
  @media screen and(min-width:1280px) {
    flex-basis: calc((100% - 60px) / 3);
  }
  &:not(:last-child) {
    @media screen and(max-width:767px) {
      margin-bottom: 38px;
    }
  }
}
.advantages__item-first::before {
  content: '';
  display: block;
  width: 33px;
  height: 50px;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url(../images/sun.png);

  @media screen and(min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(../images/sun2x.png);
  }
  @media screen and(min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
    background-image: url(../images/sun3x.png);
  }
}

.advantages__item-second::before {
  content: '';
  display: block;
  width: 50px;
  height: 50px;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url(../images/apple.png);

  @media screen and(min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(../images/apple2x.png);
  }
  @media screen and(min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
    background-image: url(../images/apple3x.png);
  }
}

.advantages__item-third::before {
  content: '';
  display: block;
  width: 44px;
  height: 50px;
  background-repeat: no-repeat;
  background-size: contain;
  background-image: url(../images/heart.png);

  @media screen and(min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2dppx) {
    background-image: url(../images/heart2x.png);
  }
  @media screen and(min-device-pixel-ratio: 3), (min-resolution: 288dpi), (min-resolution: 3dppx) {
    background-image: url(../images/heart3x.png);
  }
}

