.advantage__text {
  margin-top: 16px;
  margin-bottom: 15px;

  font-family: 'Titan One', cursive;
  font-size: 36px;
  line-height: 1.14;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: #d41443;

  @media screen and (min-width: 768px) {
    margin-top: 36px;
    font-size: 48px;
    line-height: 1.15;
    text-transform: none;
  }

  @media screen and (min-width: 1280px) {
    margin-top: 40px;
    font-size: 58px;
    line-height: 1.14;
  }
}

.advantage__description {
  font-weight: 700;
  font-size: 12px;
  line-height: 1.7;
  letter-spacing: 0.02em;
  color: #000000;

  @media screen and(min-width:768px) {
    font-size: 14px;
    line-height: 1.74;
  }

  @media screen and(min-width:1280px) {
    font-size: 16px;
    line-height: 1.76;
  }
}
